import { FileBrowserViewModel } from "../ViewModel/FileBrowserViewModel";
export class FtpFileBrowserViewModel extends FileBrowserViewModel {
  /// <summary>
  ///  遠端連線編號
  /// <summary>
  public remote_conn_id: string;
  /// <summary>
  ///  連線類型主鍵(參考共用代碼006)
  /// <summary>
  public conn_type_key: string;

  /// <summary>
  ///  壓密方法主鍵(參考共用代碼046)
  /// <summary>
  public encrypt_method_key:string;
/// <summary>
  ///  本機檔案路徑
  /// <summary>
  public local_filepath:string;

  /// <summary>
///  遠端連線字串
/// <summary>
public remote_conn_string: string;
/// <summary>
///  連線類型
/// <summary>
public conn_type: string;
/// <summary>
///  壓密方法
/// <summary>
public encrypt_method: string;


}



